html, body {
  font-family: Arial,Helvetica,sans-serif;
  color: black;
}

.black-gradient {
  background: #a0a0a0;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: radial-gradient(ellipse at center, #a0a0a0 0%, black 99%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a0a0a0', endColorstr='#000000',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  color: white;
}

img {
  max-width: 100%;
}

a, a:visited, a:hover {
  color: blue;
}

.row {
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1;
      flex: 1;
}

.black {
  background-color: #222;
  box-shadow: inset 0px 0px 10px 10px rgba(0, 0, 0, 0.5);
}

.text-left {
  text-align: left;
}

.inline-block {
  display: inline-block;
}

.large-padding {
  padding: 10px 15px;
}

.main-header {
  padding: 2em;
  padding-left: 10%;
  padding-right: 10%;
  text-align: center;
}

.main-header img {
  vertical-align: middle;
}

.main-header .text {
  width: 300px;
  text-align: left;
  display: inline-block;
  vertical-align: middle;
}

.btn {
  padding: 10px 15px;
  background: #e2e2e2;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #e2e2e2 0%, #dbdbdb 50%, #d1d1d1 51%, #fefefe 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e2e2e2', endColorstr='#fefefe',GradientType=0 );
  /* IE6-9 */
  color: black !important;
  text-decoration: none;
  display: inline-block;
  margin: 1em 0;
}

.title {
  font-weight: bolder;
  font-size: 1.8em;
  text-transform: uppercase;
}

.text-center {
  text-align: center;
}

.top-space {
  margin-top: 1em;
}

.big-padding {
  padding: 30px;
}

.container {
  margin: 0 auto;
  width: 960px;
  max-width: 100%;
}

.big-margin-top {
  margin-top: 80px;
}

.subtitle {
  font-size: 1.5em;
}

ul li {
  margin-top: 1em;
}

.items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  text-align: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.items .item {
  width: 250px;
  max-width: 100%;
  border: double white 2px;
  margin: 1em;
  padding: 10px;
}

.course {
  width: 250px;
  max-width: 100%;
  border: double white 2px;
  margin: 1em;
  padding: 10px;
  display: inline-block;
  border-color: black;
  vertical-align: top;
}
